%arrowPositionAndWidth {
  position: absolute;
  width: 10px;
}

%circle {
  height: 20px;
  width: 20px;
  border: 2px solid black;
  background-color: white;
  border-radius: 50%;
}

%flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

%transformArrows {
  transform-origin: bottom center;
}
